#p-inner-content {
    overflow-y: scroll;
    position: absolute;
    // top: 67px;
    top: 52px;
    left: 17%;
    width: 83%;
    height: 100%;
    background: #e5e5e5;
    transition: .6s;
    .top-nav-border-wrap {
        width: 100%;
        .top-nav-border {
            box-shadow: 0px 3px 7px rgba(166, 182, 182, 0.2);
            height: 2px;
        }
    }
    .right-content {
        .top-text-box {
            p {
                font-family: $base-font-family;
                font-weight: 300;
                font-size: 24px;
                line-height: 35px;
                letter-spacing: 0.03em;
                color: #3d4444;
                margin-left: 3%;
            }
        }
        .inner-wrap {
            padding-bottom: 9%;
            #progresses-wrap {
                .p-indexes {
                    @extend %large-container;
                    display: flex;
                    justify-content: space-evenly;
                    .index-wrap {
                        text-align: center;
                        width: 30%;
                        .top-text {
                            .index-name {
                                font-family: $base-font-family;
                                font-weight: bold;
                                font-size: 16px;
                                line-height: 18px;
                                letter-spacing: 0.05m;
                                color: #9ea5a5;
                            }
                            .index-detail {
                                font-family: $base-font-family;
                                font-weight: normal;
                                font-size: 14px;
                                line-height: 20px;
                                letter-spacing: 0.05em;
                                color: #3d4444;
                            }
                        }
                        .index-chart {
                            .progress-wrap {
                                text-align: center;
                                // position: absolute;
                                // top: 87px;
                                // right: 0;
                                // left: 0;
                                .progress-pie-chart {
                                    margin: 3% auto;
                                    width: $pie-size;
                                    height: $pie-size;
                                    border-radius: 50%;
                                    background: #f2f2f2;
                                    position: relative;
                                    &.gt-50 {
                                        // background: linear-gradient(#87d7c4, #c2e2a3, #87d7c4);
                                        background: linear-gradient(#87d7c4, #c2e2a3);
                                    }
                                    .ppc-progress {
                                        @include circle($pie-size);
                                        clip: rect(0, $pie-size, $pie-size, #{$pie-size / 2});
                                        .ppc-progress-fill {
                                            @include circle($pie-size);
                                            clip: rect(0, #{$pie-size / 2}, $pie-size, 0);
                                            background: linear-gradient(#c2e2a3, #87d7c4);
                                            transform: rotate(60deg);
                                        }
                                        &.gt-50 {
                                            clip: rect(0, #{$pie-size/2}, $pie-size, 0);
                                            .ppc-progress-fill {
                                              clip: rect(0, $pie-size, $pie-size, #{$pie-size / 2});
                                              background: #f2f2f2;
                                            }
                                        }
                                    }
                                    .ppc-percent {
                                        @include circle(#{$pie-size / 1.15});
                                        background: #fff;
                                        text-align: center;
                                        display: table;
                                        .percent-inner-wrap {
                                            display: table-cell;
                                            vertical-align: middle;
                                            span {
                                                display: block;
                                                color: #9ea5a5;
                                                font-family: $base-font-family;
                                                font-weight: 300;
                                                font-size: 26px;
                                                line-height: 39px;
                                            }
                                        }
                                    }
                                }                            
                            }
                        }
                    }
                }
                .personal-progress {
                    @extend %large-container;
                    display: flex;
                    .left-content {
                        width: 50%;
                        margin-left: 1.9%;
                        .top-text {
                            margin-bottom: 4px;
                            p {
                                font-family: $base-font-family;
                                font-weight: 600;
                                font-size: 16px;
                                line-height: 24px;
                                letter-spacing: 0.05em;
                                color: #9ea5a5;
                                margin-bottom: 0;
                                margin-top: 5%;
                            }
                        }
                        progress {
                            // position: absolute;
                            // top: 43%;
                            // right: 3%;
                            // width: 26%;
                            background: #e3e3e3;
                            border-radius: 20px;
                            width: 82%;
                            display: inline-block;
                        }
                        progress::-moz-progress-bar {
                            background: #7ad2cc;
                            border-radius: 20px;
                        }
                        @media screen and (-webkit-min-device-pixel-ratio: 0) {
                            // progress {
                            //     height: 25px;
                            // }
                        }
                        progress::-webkit-progress-bar {
                            background: transparent;
                        }
                        progress::-webkit-progress-value {
                            border-radius: 20px;
                            background: #7ad2cc;
                        }
                        .outp-label {
                            // position: absolute;
                            // top: 29%;
                            // right: 5%;
                            font-family: $base-font-family;
                            font-weight: 300;
                            font-size: 11px;
                            line-height: 170%;
                            letter-spacing: 0.04em;
                            color: #3d4444;
                            display: inline-block;
                            vertical-align: middle;
                        }
                    }
                    .right-content {
                        width: 50%;
                        .top-text {
                            p {
                                font-family: $base-font-family;
                                font-weight: 600;
                                font-size: 16px;
                                line-height: 24px;
                                letter-spacing: 0.05em;
                                color: #9ea5a5;
                                margin-bottom: 0;
                                margin-top: 5%;
                            }
                        }
                        .bottom-text {
                            p {
                                font-family: $base-font-family;
                                font-size: 14px;
                                line-height: 26px;
                                letter-spacing: 0.05em;
                                color: #3d4444;
                                margin-top: 9px;
                                margin-bottom: 8%;
                            }
                        }
                    }
                }
                .members-progresses {
                    @extend %large-container;
                    position: relative;
                    height: 240px;
                    width: 93%;
                    overflow: hidden;
                    .top-text {
                        p {
                            font-family: $base-font-family;
                            font-weight: 600;
                            font-size: 16px;
                            line-height: 24px;
                            letter-spacing: 0.05em;
                            color: #9ea5a5;
                            margin-bottom: 0;
                            margin-top: 0;
                            margin-left: 1.4%;
                            padding-top: 2%;
                        }
                    }
                    .glide__track {
                        position: absolute;
                        left: -9%;
                        .glide__slides {
                            // margin-left: 104%;
                            // width: 100% !important;
                            .member-wrap {
                                width: 8.8% !important;
                                .member-name {
                                    font-family: $base-font-family;
                                    font-weight: bold;
                                    line-height: 26px;
                                    letter-spacing: 0.05em;
                                    color: #9ea5a5;
                                }
                                .task-name {
                                    font-family: $base-font-family;
                                    font-size: 16px;
                                    line-height: 26px;
                                    letter-spacing: 0.05em;
                                    color: #3d4444;
                                }
                                progress {
                                    // position: absolute;
                                    // top: 43%;
                                    // right: 3%;
                                    // width: 26%;
                                    background: #e3e3e3;
                                    border-radius: 20px;
                                    width: 57%;
                                    display: inline-block;
                                }
                                progress::-moz-progress-bar {
                                    background: #7ad2cc;
                                    border-radius: 20px;
                                }
                                @media screen and (-webkit-min-device-pixel-ratio: 0) {
                                    // progress {
                                    //     height: 25px;
                                    // }
                                }
                                progress::-webkit-progress-bar {
                                    background: transparent;
                                }
                                progress::-webkit-progress-value {
                                    border-radius: 20px;
                                    background: #7ad2cc;
                                }
                                .outp-label {
                                    // position: absolute;
                                    // top: 29%;
                                    // right: 5%;
                                    font-family: $base-font-family;
                                    font-weight: 300;
                                    font-size: 11px;
                                    line-height: 170%;
                                    letter-spacing: 0.04em;
                                    color: #3d4444;
                                    display: inline-block;
                                    vertical-align: middle;
                                }
                            }
                        }
                    }
                }
                .reorg {
                    @extend %large-container;
                    display: flex;
                    .left-content {
                        width: 50%;
                        .top-text {
                            p {
                                font-family: $base-font-family;
                                font-weight: 600;
                                font-size: 16px;
                                line-height: 24px;
                                letter-spacing: 0.05em;
                                color: #9ea5a5;
                                margin-bottom: 0;
                                margin-top: 0;
                                margin-left: 2.4%;
                                padding-top: 4%;
                            }
                        }
                        .bottom-text {
                            p {
                                text-align: center;
                                font-family: $base-font-family;
                                font-size: 14px;
                                line-height: 26px;
                                letter-spacing: 0.05em;
                                color: #3d4444;
                            }
                        }
                    }
                    .right-content {
                        width: 50%;
                        .top-text {
                            p {
                                font-family: $base-font-family;
                                font-weight: 600;
                                font-size: 16px;
                                line-height: 24px;
                                letter-spacing: 0.05em;
                                color: #9ea5a5;
                                margin-bottom: 0;
                                margin-top: 4%;
                            }
                        }
                        .bottom-text {
                            p {
                                text-align: center;
                                font-family: $base-font-family;
                                font-size: 14px;
                                line-height: 26px;
                                letter-spacing: 0.05em;
                                color: #3d4444;
                            }
                        }
                    }
                }
            }
        }
    }
}

.p-inner-content-after {
    left: 6% !important;
    width: 94% !important;
    transition: .6s;
}

