@media screen and (max-width: 800px) {
    #p-inner-content {
        .right-content {
            .inner-wrap {
                #progresses-wrap {
                    .members-progresses {
                        .glide__track {
                            // left: -56%;
                            .glide__slides {
                                .member-wrap {
                                    width: 17% !important;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

