@media screen and (max-width: 580px) {
    #p-inner-content {
        .right-content {
            .inner-wrap {
                padding-bottom: 22%;
                #progresses-wrap {
                    .p-indexes {
                        display: block;
                        margin: 0 8% 7%;
                        padding-bottom: 8px;
                        .index-wrap {
                            text-align: center;
                            width: 90%;
                            margin: 0 auto;
                            .top-text {
                                .index-name {
                                    margin: 0;
                                    padding-top: 17px;
                                }
                                .index-detail {
                                    margin-top: 5px;
                                }
                            }
                        }
                    }
                    .personal-progress {
                        margin: 0 8% 7%;
                        display: block;
                        padding-bottom: 17px;
                        .left-content {
                            width: 100%;
                            .top-text {
                                padding-top: 1px;
                                padding-left: 6px;
                            }
                            progress {
                                margin-left: 6px;
                            }
                        }
                        .right-content {
                            width: 100%;
                            .top-text {
                                padding-left: 11px;
                            }
                            .bottom-text {
                                padding-left: 11px;
                                p {
                                    margin-bottom: 0;
                                    text-align: center;
                                }
                            }
                        }
                    }
                    .members-progresses {
                        margin: 0 8% 7%;
                        .top-text {
                            padding-top: 1px;
                            padding-left: 6px;
                        }
                    }
                    .reorg {
                        margin: 0 8% 7%;
                        display: block;
                        padding-bottom: 17px;
                        .left-content {
                            width: 100%;
                            .top-text {
                                padding-top: 1px;
                                padding-left: 6px;
                            }
                        }
                        .right-content {
                            width: 100%;
                            .top-text {
                                padding-left: 11px;
                            }
                            .bottom-text {
                                p {
                                    margin-bottom: 0;
                                    text-align: center;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}